.footer {
  background-color: #171F3F;
  color: white; }
.footer__wrapper {
  margin: 0 auto;
  height: auto;
  width: 100%;
  max-width: 87vw;
  padding:  6rem 0;
  position: relative; }
.footer__h2 {
  margin: 0;
  font-size: 2rem;
  &::before {
    content: "";
    border-top: 3px solid #0593FB;
    width: 4rem;
    display: block;
    border-radius: 0.5rem;
    margin-bottom: 1rem; } }
.footer__paragraph {
  font-weight: 300;
  font-size: 1rem;
  line-height: 2rem; }
.footer__icons {
  display: flex;
  list-style: none;
  padding: 0;
  margin-bottom: 2rem; }
.footer__icon {
  display: block;
  width: 32px;
  height: 32px;
  margin-right: 1.5rem; }
.footer__icons-fb {
  background-image: url("../assets/img/fb.svg"); }
.footer__icons-gh {
  background-image: url("../assets/img/gh.svg"); }
.footer__icons-insta {
  background-image: url("../assets/img/Instagram.svg"); }
.footer__email {
  color: #0593FB;
  border-bottom: 2px solid #0593FB;
  text-decoration: none;
  margin-bottom: 1rem; }

@media (max-width : 375px) and (orientation: portrait) {
  .footer__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding:  4rem 0; }
  .footer__h2 {
    font-size: 1.6rem;
    align-self: start; }
  .footer__icons {
    margin: 2rem 0 3rem 0; }
  .footer__paragraph {
    font-size: 1rem;
    line-height: 1.15rem; }
  .footer__icon {
    margin-right: 0; }
  .footer__icons-gh {
    margin: 0 2rem; }
  .footer__email {
    text-align: center; } }

@media (min-width : 410px) and (orientation: portrait) {
  .footer__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding:  4rem 0; }
  .footer__h2 {
    font-size: 1.8rem;
    align-self: start; }
  .footer__icons {
    margin: 2rem 0 3rem 0; }
  .footer__paragraph {
    font-size: 1.1rem;
    line-height: 1.5rem; }
  .footer__icon {
    margin-right: 0; }
  .footer__icons-gh {
    margin: 0 2rem; }
  .footer__email {
    text-align: center;
    font-size: 1.2rem; } }

@media (min-width : 768px) and (orientation: portrait) {
  .footer__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding:  4rem 0; }
  .footer__h2 {
    font-size: 2.2rem;
    align-self: start; }
  .footer__icons {
    margin: 2rem 0 3rem 0; }
  .footer__paragraph {
    font-size: 1.3rem;
    line-height: 1.7rem; }
  .footer__icon {
    margin-right: 0; }
  .footer__icons-gh {
    margin: 0 4rem; }
  .footer__email {
    text-align: center;
    font-size: 1.3rem; } }

@media (min-width: 640px) and (max-width:823px) and(orientation: landscape) {
  .footer__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding:  4rem 0 3rem 0; }
  .footer__h2 {
    font-size: 1.7rem;
    align-self: start; }
  .footer__icons {
    margin: 2rem 0 3rem 0; }
  .footer__paragraph {
    font-size: 1.1rem;
    line-height: 1.5rem; }
  .footer__icon {
    margin-right: 0; }
  .footer__icons-gh {
    margin: 0 3rem; }
  .footer__email {
    text-align: center;
    font-size: 1.2rem; } }






