.projects {
  background-color: #F5F7FA;
  padding: 8rem 0;
  margin-top: 5rem;
  overflow: hidden; }
.projects__wrapper {
  margin: 0 auto;
  height: auto;
  max-width: 87vw; }
.projects__h2 {
  font-size: 2rem;
  text-align: left; }
.projects__paragraph {
  font-weight: 300; }
.projects__grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  grid-row-gap: 2rem;
  grid-column-gap: 1rem;
  margin-top: 4rem; }
.project-card {
  width: 35vw;
  height: auto;
  padding: 2rem 3rem 3rem 3rem;
  box-shadow: 0px 0.5rem 1rem rgba(24, 41, 67, 0.09);
  border-radius: 0.5rem;
  background-color: #FFFFFF;
  position: relative; }
.project-card__h4 {
  margin: .5rem 0 .5rem 0; }
.list-wrapper__ul {
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  justify-items: center;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: #F5F7FA;
  margin: 0 auto;
  width: 100%;
  height: 20%;
  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
  padding: 0; }
.list-wrapper__li {
  margin: 0 1rem; }
.list-wrapper__a {
  position: relative;
  top: 25%;
  margin-right: 2.5rem;
  font-size: 1.1rem; }

.list-wrapper__a--demo {
  text-decoration: none;
  color: #0593FB;
  background-color: transparent;
  font-weight: 500;
  &::before {
    content: "";
    display: block;
    width: 24px;
    height: 24px;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url("../assets/img/demo.svg");
    position: absolute;
    left: -28px;
    bottom: 0; } }
.list-wrapper__a--github {
  text-decoration: none;
  color: #0593FB;
  background-color: transparent;
  font-weight: 500;
  &::before {
    content: "";
    display: block;
    width: 24px;
    height: 24px;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url("../assets/img/code.svg");
    position: absolute;
    left: -28px;
    bottom: 0; } }
.project-card__paragraph {
  margin-bottom: 2rem;
  font-weight: 300;
  line-height: 1.5rem; }

